body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:lightgray}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.app{margin:0 250px;padding:0 50px}.app .under-construction{text-align:center;margin-top:50px}.app .under-construction img{width:800px}.primary-button,.secondary-button{border:2px solid black;border-radius:5px;padding:10px 25px;margin:5px;font-size:20px;font-weight:bold;text-decoration:none}.primary-button:hover,.secondary-button:hover{background-color:deeppink;cursor:pointer}.primary-button{color:cyan;background-color:deeppink}.primary-button:hover{background-color:hotpink}.secondary-button{color:purple;background-color:cyan}.secondary-button:hover{background-color:deepskyblue}.center{text-align:center}

.nav-bar{text-align:center}.nav-bar .unicorn-banner{height:400px}

.footer{background-color:white;padding:25px;margin-top:50px}.footer .footer-container{display:grid;grid-template-columns:1fr 1fr;align-items:center;justify-items:center;margin:0 100px}.footer .footer-container .column{display:grid;grid-template-columns:1fr;justify-items:center;font-size:20px;font-weight:bold}.footer .footer-container .column a{margin:5px;text-decoration:none;color:black}.footer .footer-container .column a:hover{cursor:pointer;color:hotpink}

.home{text-align:center;margin-top:20px}.home .race-tile-container{display:grid;grid-template-columns:1fr 1fr 1fr}.home .race-tile-container .race-tile{margin:0 25px}.home .course-image{margin:50px 0}.home .course-image img{height:400px}.home .course-description{margin-top:100px;font-size:25px}


.race-page{margin-top:30px}.race-page .race-controls{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;justify-items:center;text-align:center;margin:30px 0}.race-page .race-controls .button{margin:5px 15px}.race-page .header-row{display:grid;grid-template-columns:1fr 1fr;align-items:center;justify-items:center;margin:50px 0 25px 0}.race-page .header-row div{font-size:30px;font-weight:bold;text-align:center}.race-page .header-row div .title{font-size:25px;font-style:italic}.race-page .header-row .secondary-button{display:table;height:100px;padding:0;max-width:270px}.race-page .header-row .secondary-button div{display:table-cell;vertical-align:middle;padding:10px 20px;text-align:center;font-weight:bold;font-size:20px}.race-page .introduction-text{margin-bottom:50px}.race-page .course-image{text-align:center}.race-page .course-image img{max-width:700px}.race-page .course-description-container{margin:50px 0}.race-page .cut-off-times-container{margin-bottom:50px}.race-page .cut-off-times-container .cut-off-time-description{margin-bottom:5px}.race-page .header{font-size:30px;font-weight:bold;text-decoration:underline;margin-bottom:10px}.race-page .description-text{font-size:25px}

